  searchTreeForChecked(tags, nodes){
    //console.log("searchTreeForChecked(",tags,", ",nodes,")");
    for (var node in nodes){
      var found=false;
      //console.log(tags);
      for (var tag in tags){
        //console.log(tags[tag]," = ",nodes[node].label);
        if (!nodes[node].id){
          if (nodes[node].label==tags[tag]['value']){
            found=true
          }
        }
        else {
          if (nodes[node].label==tags[tag]['value']&nodes[node].id==tags[tag]['id']){
            found=true
          }
        }
      }
      if (found){
        //console.log(nodes[node].label," found, set label=true");
        nodes[node]["checked"]=true;
      }
      else{
        //console.log(nodes[node].label,"not found, set label=false");
          nodes[node]["checked"]=false;
      }
      if (nodes[node].children){
        //console.log("go for Children in node",nodes[node].label);
        this.searchTreeForChecked(tags,nodes[node].children);
        }
    }
  }
  searchPredicate(node, searchTerm) {
    var result = false;
    if (node.search){
      result = node.search && node.search.toLowerCase().indexOf(searchTerm) >= 0;
    }
    else {
      {
        result = node.label && node.label.toLowerCase().indexOf(searchTerm) >= 0;
      }
    }
    return result;
  }

